/* Background gradient */
.landing-container {
  min-height: 100vh;
  background: linear-gradient(to right, #2b1055, #190537);
  color: #fff;
  font-family: "Poppins", sans-serif;
  position: relative;
  z-index: 1;
}

/* Header */
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 25px 60px;
}

.nav {
  display: flex;
  gap: 30px;
}

.nav a {
  text-decoration: none;
  color: #ddd;
  font-size: 24px;
  transition: color 0.3s;
}

.nav a:hover {
  color: #fff;
}

.wallet-btn {
  background: #2563eb;
  border: none;
  padding: 10px 20px;
  border-radius: 25px;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  transition: background 0.3s;
}

.wallet-btn:hover {
  background: #1d4ed8;
}

/* Hero Section */
.hero-section {
  display: flex;
  justify-content: center;   /* ✅ 改成居中，而不是 space-between */
  align-items: center;
  padding: 60px 80px;
  flex-wrap: wrap;
  gap: 100px;   /* ✅ 增加间距控制 */
  max-width: 1200px; /* ✅ 限制最大宽度，避免内容散开 */
  margin: 0 auto;   /* ✅ 居中对齐 */
}

.hero-text {
  max-width: 600px;
}


.hero-text h1 {
  font-size: 52px;
  font-weight: 900;
  line-height: 1.2;
  transform: scaleX(1.3); /* 横向拉伸 1.1 倍 */
  display: inline-block;  
}

.hero-text p {
  margin-top: 20px;
  color: #ccc;
  line-height: 1.6;
}

/* CTA button */
.discover-btn {
  margin-top: 30px;
  background: #2563eb;
  border: none;
  padding: 14px 28px;
  border-radius: 30px;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  font-size: 16px;
  transition: background 0.3s;
}

.discover-btn:hover {
  background: #1d4ed8;
  transform: scale(1.05);
  box-shadow: 0 0 15px rgba(37, 99, 235, 0.6);
}


.arrow {
  margin-left: 10px;
  font-size: 18px;
}

/* Stats */
.stats {
  display: flex;
  gap: 50px;
  margin-top: 40px;
}

.stats div {
  padding-right: 40px;
  border-right: 1px solid rgba(255,255,255,0.2);
}
.stats div:last-child {
  border-right: none;
}


.stats h3 {
  font-size: 24px;
  font-weight: bold;
}

.stats p {
  font-size: 14px;
  color: #aaa;
}

/* Right Side Image */
.hero-image {
  position: relative;
  width: 400px;
  height: 500px;
  margin-top: 40px;
}

.bg-card {
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 12px;
}

.card1 {
  top: 20px;
  left: 20px;
  background: linear-gradient(to right, #9333ea, #ec4899);
  z-index: 1;
}

.card2 {
  top: 10px;
  left: 10px;
  background: linear-gradient(to right, #3b82f6, #1e3a8a);
  z-index: 2;
}

.main-img {
  position: relative;
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 12px;
  z-index: 3;
}



.darkveil-canvas {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;

  z-index: -1;             /* ✅ 在所有内容后面 */
  pointer-events: none;    /* ✅ 不挡点击/交互 */

  background: radial-gradient(circle at center, #2a0845, #1a1033);
  /* 你 DarkVeil 的实际样式，比如动效、canvas 等 */
}


.profile-grid-container {
  margin-left:10%;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 20px 200px; /* 垂直24px，水平32px */
  padding: 10px 5%; /* 上下 40px，左右各10%空隙 */
  justify-content: center;
  box-sizing: border-box;
}
